.main-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 500px;
  max-width: var(--max-width);
  margin: 0 auto;
}

.game-field {
  position: relative;
  width: 400px;
  height: 413px;
  background: rgb(128, 211, 207);
}

.css-2,
.css-3,
.css-4 {
  width: 158px;
  height: 134px;
  min-width: 1rem;
  min-height: 1rem;
  position: absolute;
  display: inline-flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  flex-grow: 1;
  flex-wrap: wrap;
  background: var(--icon-hex) center center no-repeat;
}

.css-3 {
  width: 80px;
  height: 69px;
}

.css-4 {
  width: 54px;
  height: 47px;
}
